import uniTag from "@/components/uni-tag/uni-tag.vue"
import http from '@/components/utils/http.js'
import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	components: {
		uniTag
	},
	computed: {
		...mapState(['isLogin', 'forcedLogin', 'user_id', 'group_id'])
	},
	onLoad() {
		let that=this;
		that.userInfo = Object.assign({}, that.userInfo, this.$store.state.userInfo);
		//console.log(this.$store.state.userInfo);
	},
	data() {
		return {
			userInfo:{
				id:0,
				user_id:0,
				group_id:0,
				username:"游客",
				nickname:"游客",
				mobile:"",
				avatar:"",
			}
		}
	},
	methods: {
		photoUpload(e) {
			var type = e.currentTarget.dataset.type
			var that=this;
			uni.chooseImage({
				count: 1,
				sizeType: "compressed",
				success: (res) => {
					//tempFilePaths
					res.tempFilePaths.forEach(function(value, index) {
						uni.uploadFile({
							url: http.baseUrl + "/api/common/upload",
							filePath: value,
							name: "file",
							header: {
								'token': uni.getStorageSync('token'),
								'X-Requested-With': 'XMLHttpRequest',
								'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
							},
							success: (res) => {
								if (res.statusCode == 200) {
									var resData = JSON.parse(res.data);
									//console.log(resData);
									if (typeof resData == "object" && typeof resData.code != "undefined" && resData.code == 1) {
										//图片上传成功
										http.apiRequest({
											api: "/api/user/profile",
											method: "POST",
											data: {
												avatar: resData.data.url,
												gender: 1,
											},
											secuss: (res, httpStatus) => {
												if (res.code == 1) {
													that.$store.state.userInfo.avatar=resData.data.url;
													that.userInfo.avatar=resData.data.url;
													uni.$emit("userInfoUpdate",{});
													uni.showToast({
														title: "上传头像成功！"
													});
												}
											},
										
										});
										
										
									}
		
								} else {
									//图片上传失败
								}
							},
							fail: (res) => {},
						});
					});
				},
				fail: () => {},
			});
		},
	}
}
